<template>
<div class="selector">
    {{title || 'ជ្រើសរើសក្រាហ្វិក៖'}}
    
      
    <select v-model="selectedItem" @change="selectChanged">
        <option value="0">សង្ខេបរួម</option>
        <option value="1">បែងចែកតាមឆ្នាំ</option>
        <option value="2">បែងចែកតាម​ខេត្ត</option>
        <option value="3">បែងចែកតាម​ចំណាត់ថ្នាក់គម្រោង</option>
        <option value="4">បែងចែកតាម​ប្រភេទគម្រោង</option>
    </select>
</div>
    
</template>

<script>

export default {
    name: 'ChartAbstract',
    data(){
        return{
            selectedItem: "0"
        }
    },
    props:{
        title: String,
    },
    methods:{
        selectChanged(item){
            this.$emit('selectChanged', this.selectedItem);
        }
    }
}
</script>

<style scoped>

.selector {
    font-family: 'Nokora', sans-serif;
    font-size: 14px;
}
.selector > select {
    font-family: 'Nokora', sans-serif;
    font-weight: 700;
    font-size: 14px;
}
.selector > select > option {
    font-family: 'Nokora', sans-serif;
    font-size: 14px;
}
</style>