export default {
    en2kh(en) {
        const khm = '០១២៣៤៥៦៧៨៩';
        const eng = '0123456789';
        var kh = en;
        for (let i = 0; i < eng.length; i++) {
            kh = kh.replaceAll(eng[i], khm[i]);
        }
        return kh;
    },
    format_date(date) {
        var parts = date.split('-');
        if(parts.length !==3) return date;
        return parts[2] + '-' + parts[1] + '-' + parts[0];
    },

    html_row(header, value, prefix) {
        return '<tr>\
                    <th scope="row">' + header + '</th>\
                    <td>' + value + prefix + '</td>\
                </tr>\n';
    },

    row(feature, property_name, title, prefix = '') {
        var value = feature.properties[property_name];
        return  value !== null ? this.html_row(title, value, prefix) : '';
    },

    row_kh(feature, property_name, title, prefix = '') {
        var value = feature.properties[property_name];
        if(value == null) return '';
        return  this.html_row(title, this.en2kh(value), prefix);
    },

    row_date(feature, property_name, title, prefix = '') {
        var value = feature.properties[property_name];
        if(value == null) return '';
        value = this.format_date(value);
        return  this.html_row(title, this.en2kh(value), prefix);
    },

    create_table(feature) {
        var table = '<table>\n';
            table += this.row(feature, 'owner_kh',   'Owner KH');
            table += this.row(feature, 'owner_en',   'Owner EN');
            table += this.row(feature, 'type',       'Type');
            table += this.row(feature, 'area_li_ha', 'Size', ' Ha');
            table += this.row(feature, 'lic_type',   'License');
            table += this.row(feature, 'loc_khet',   'Provice');
            table += this.row(feature, 'loc_srok',   'District');
            table += this.row(feature, 'loc_khum',   'Commune');
            table += this.row(feature, 'loc_phum',   'Village');
            table += this.row(feature, 'loc_name',   'Location Name');
            //table += this.row(feature, 'loc_other',  'Other Name');
            table += this.row_date(feature, 'lic_date',   'Issued');
            table += this.row_kh(feature, 'lic_number', 'Number', ' សជណ.បស្ថ');
            //table += this.row(feature, 'lic_expiry', 'Expiry');
            // table += this.row(feature, 'lic2_date',  'lic2_date');
            // table += this.row(feature, 'lic2_num',   'lic2_num', ' សជណ.បស្ថ');
            // table += this.row(feature, 'lic2_exp',   'lic2_exp');
            table += this.row(feature, 'status',     'Status');
            table += this.row(feature, 'team',       'Team');
            table += this.row(feature, 'added_by',   'Added by');
            // table += this.row(feature, 'edited_by',  'Edited by');
            table += this.row(feature, 'reference',  'Reference');
            table += this.row(feature, 'pro_code',   'Code');
            table += this.row(feature, 'remark',     'Remark');
            table += '</table>';

            return table;
    }
}