import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import func from '../scripts/helpers.js';

export default new Vuex.Store({
  state: {
    projects: {},
    windowWidth: 0,
    windowHeight: 0
  },
  mutations: {
    initProjectList(state, projects) {
      state.projects = projects;
    },
    windowSize(state, width, height){
      state.windowWidth = width;
      state.windowHeight = height;
    }
  },

  getters: {
    geojson: (state, getters) => {
      return state.projects;
    },
    esiaGeoJson: (state, getters) =>{
      return func.filterGeoJson(getters.geojson, 'lic_type', 'ESIA')
    },
    iesiaGeoJson: (state, getters) =>{
      return func.filterGeoJson(getters.geojson, 'lic_type', 'IESIA')
    },
    epcGeoJson: (state, getters) =>{
      return func.filterGeoJson(getters.geojson, 'lic_type', 'EPC')
    },

    windowWidth: (state) => {
      return state.windowWidth;
    }
  },

  actions: {
  },
  modules: {
  }
})
