<template>
  <div 
      @mouseover="toggleShowMenu = true" 
      @mouseleave="toggleShowMenu = false"
      >

    <ul  :class="'main-menu ' + (value? 'button-close':'') " @click="$emit('input', !value)">
      <li>
        <a href="#">
          <i :class="mainMenuClass" aria-hidden="true" />
          {{ value ? "Cancal" : "" }}
        </a>
      </li>
    </ul>

    <div v-if="value" >

      Layers:

      <div class="layer-control">
        <label for="esia-layer" @click="onLayerSelected('esia-layer-checked', toggleShowEsiaLayer)">
            <input type="checkbox" v-model="toggleShowEsiaLayer"  id="esia-layer">
            <i class="far fa-square"  :style="esiaLegendStyle" aria-hidden="true"></i>
            ESIA
          </label>
      </div>

      <div class="layer-control">
        <label for="iesia-layer" @click="onLayerSelected('iesia-layer-checked', toggleShowIesiaLayer)">
            <input type="checkbox" v-model="toggleShowIesiaLayer"  id="iesia-layer">
            <i class="far fa-square"  :style="iesiaLegendStyle" aria-hidden="true"></i>
            IESIA
          </label>
      </div>
      
      <div class="layer-control">
        <label for="epc-layer" @click="onLayerSelected('epc-layer-checked', toggleShowEpcLayer)">
            <input type="checkbox" v-model="toggleShowEpcLayer"  id="epc-layer">
            <i class="far fa-square"  :style="epcLegendStyle" aria-hidden="true"></i>
            EPC
          </label>
      </div>

      <br>
      Basemaps:

      <div class="basemaps" >
        <div v-for="b in basemaps" :key="b.id">
          <label :for="b.id" @click="onBasemapChanged(b)">
            <input type="radio" v-model="selectedBasemap" :name="b.name" :value="b.id" :id="b.id">
            <img :src="rasterIcon" width="18"/>
            {{b.name}}
          </label>
        </div>
      </div>


      <br>
      
    </div>


  </div>
</template>

<script>
import rasterIcon from '@/assets/raster.png';
import basemaps from "@/data/basemaps.json";
import InputRadio from '@/components/InputRadio.vue';
import consts from '@/scripts/consts.js';

export default {
  name: "PopMenu",
  props:{
    value: {
      type: Boolean,
      default: false
    }
  },
  components:{
    InputRadio
  },

  data() {
    return {
      // toggleShowMenu: this.expanded,
      selectedBasemap: "0",
      toggleShowEsiaLayer: true,
      toggleShowIesiaLayer: true,
      toggleShowEpcLayer: true,
      basemaps,
      rasterIcon
    };
  },
  methods: {
    onBasemapChanged(basemap){
      this.$emit('onBasemapChanged', basemap);
    },
    onLayerSelected(eventName, value){
      this.$emit(eventName, !value);
    }
  },
  computed:{
    toggleShowMenu:{
      get: function() {return this.expanded;},
      set: function(value) {this.$emit('input', value);},
    },
    mainMenuClass(){
      return this.value? 'fa fa-times button-close':'fa fa-layer-group';
    },
    esiaLegendStyle() {
      return `color:${consts.colors.esia}; font-size:16px; font-weight:bold`;
    },
    iesiaLegendStyle() {
      return `color:${consts.colors.iesia}; font-size:16px; font-weight:bold`;
    },
    epcLegendStyle() {
      return `color:${consts.colors.epc}; font-size:16px; font-weight:bold`;
    }
  },
  watch:{
  }
};
</script>

<style scoped>
.button-close {
    color: red;
    padding: 0 5px 0 0;
}
.button-close > li > a {
  color: red;
}
ul {
  list-style: none;
}
.context-menu {
  position: relative;
}
.main-menu {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgb(64 64 64 / 5%);
  padding: 8px 5px;
  margin: 0px 0px;
}
.main-menu > li > a {
  font-family: 'Nokora', sans-serif;
  border: 0;
  padding: 0;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  text-decoration: unset;
  
}
.fa-layer-group {
  font-size: 150%;
}
</style>
