<template>
  <div>
    
    <vue-highcharts
      :options="options"
      ref="lineCharts"
    ></vue-highcharts>
  </div>
</template>

<script>
import VueHighcharts from "vue2-highcharts";
import SeriesLabel from "highcharts/modules/series-label";
import Highcharts from "highcharts";

import store from "../store";
import func from "../scripts/helpers.js";

SeriesLabel(Highcharts);



export default {
  components: {
    VueHighcharts,
  },
  data() {
    return {
      options: null,
      Highcharts,
      
    };
  },
  methods: {
      chartSelected(index){
          this.selectedChart = index;
      },
      smallScreen() {
      return store.getters.windowWidth<600;
    }

    // load() {
    //   let lineCharts = this.$refs.lineCharts;
    //   lineCharts.delegateMethod("showLoading", "Loading...");
    //   setTimeout(() => {
    //     lineCharts.addSeries(asyncData);
    //     lineCharts.hideLoading();
    //   }, 100);
    // },
    // onClick(obj) {
    //   console.log(obj);
    // },
  },
  computed:{
    
  },
  mounted(){
    
    const geojson = store.getters.geojson;
    const projects = geojson.features;
    const countByYears = func.groupFeaturesByYears(projects);
    const { labels: years, data: projectsPerYear } = func.getLabelAndData(countByYears);
    const typesPerYear = projectsPerYear.map((y) => func.countFeaturesByTypes(y));

    // Series Data
    const esia = { type: "column", name: "ESIA", data: [] };
    const iesia = { ...esia, name: "IESIA", data: [] };
    const epc = { ...esia, name: "EPC", data: [] };
    const total = {
        type: "line",
        name: "សរុប",
        data: [],
        marker: {
            lineWidth: 2,
            lineColor: Highcharts.getOptions().colors[3],
            fillColor: "white",
        },
        };


    for (const t in typesPerYear) {
    if (Object.hasOwnProperty.call(typesPerYear, t)) {
        const type = typesPerYear[t];
        // order years ??
        const esiaCount = type["ESIA"] ?? 0;
        const iesiaCount = type["IESIA"] ?? 0;
        const epcCount = type["EPC"] ?? 0;
        esia.data = [...esia.data, esiaCount];
        iesia.data = [...iesia.data, iesiaCount];
        epc.data = [...epc.data, epcCount];
        total.data = [...total.data, esiaCount + iesiaCount + epcCount];
    }
    }

    const totalEsia = esia.data.reduce((a,b)=> a+b, 0);
    const totalIesia = iesia.data.reduce((a,b)=> a+b, 0);
    const totalEpc = epc.data.reduce((a,b)=> a+b, 0);
    const pieData = {
        type: "pie",
        name: "Total Projects",
        data: [
            {
            name: "ESIA",
            y: totalEsia,
            color: Highcharts.getOptions().colors[0], // Jane's color
            },
            {
            name: "IESIA",
            y: totalIesia,
            color: Highcharts.getOptions().colors[1], // John's color
            },
            {
            name: "EPC",
            y: totalEpc,
            color: Highcharts.getOptions().colors[2], // Joe's color
            },
        ],
        center: this.smallScreen()? [50, 50]:[100, 80],
        size: this.smallScreen()? 90:120,
        showInLegend: false,
        dataLabels: {
            enabled: false,
        },
        };

    const series = [esia, iesia, epc, total, pieData];

    const data = {
    title: {
        text: "ចំនួនគម្រោងបង្ហាញ​នៅ​លើ​ផែនទី",
    },
    chart:{
      height: 400
    },
    xAxis: {
        categories: years, //['Apples', 'Oranges', 'Pears', 'Bananas', 'Plums'],
    },
    yAxis: {
        title: {text:'ចំនួនគម្រោង'}
    },
    labels: {
        items: [
        {
            html: "ចំនួន​ទៅតាម​ចំណាត់ថ្នាក់",
            style: {
            left: this.smallScreen()?"12px":"65px",
            top: this.smallScreen()?"0px":"18px",
            color: (Highcharts.theme && Highcharts.theme.textColor) || "black",
            },
        },
        ],
    },
    credits: {
            enabled: false,
        },
    series: series
    };

    this.options = data;
  }
};
</script>

