<template>
<div class="home" >
  <LMap :style="mapScreen" 
    :bounds.sync="bounds" 
    :center.sync="center" 
    :zoom.sync="zoom" 
    :max-zoom="20" 
    :options="mapOptions"
    @mouseover="expandMenu=false"
    @mousedown="expandMenu=false"
    >

    <LTileLayer
      :key="tileProvider.name"
      :name="tileProvider.name"
      :url="tileProvider.url"
      :attribution="tileProvider.attribution"
      :minNativeZoom="tileProvider.minNativeZoom"
      :options="{ maxNativeZoom: tileProvider.maxNativeZoom, maxZoom: 100 }"
      layer-type="base"
    />

    <LLayerGroup v-if="showEsiaLayer" layer-type="overlay" name="ESIA">
      <LGeoJson :geojson="esia"  :options="options"/>
    </LLayerGroup>

    <LLayerGroup v-if="showIesiaLayer" layer-type="overlay" name="IESIA">
      <LGeoJson :geojson="iesia"  :options="options"/>
    </LLayerGroup>

    <LLayerGroup v-if="showEpcLayer" layer-type="overlay" name="EPC">
      <LGeoJson :geojson="epc"  :options="options"/>
    </LLayerGroup>


    <LLayerGroup v-if="false" layer-type="overlay" name="All Projects">
      <LGeoJson :geojson="geojson" :options="options" />
    </LLayerGroup>

    <LControl class="pop-menu">
      <PopMenu @selected="menuSelected" 
        v-model="expandMenu"
        
      />
      

    </LControl>

    <LControl class="pop-menu">
      <LayerMenu 
        v-model="expandLayerMenu"
        
        @onBasemapChanged="onBasemapChanged"
        @esia-layer-checked="toggleEsiaLayer"
        @iesia-layer-checked="toggleIesiaLayer"
        @epc-layer-checked="toggleEpcLayer"
      />
    </LControl >

    <LControl :position="'bottomleft'" class="custom-control-watermark watermark" >
        <img  :src="watermark" :width="windowWidth/(smallScreen()?3 :8)"/>
    </LControl>

  </LMap>
  <Dashboard v-if="showChart" :style="chartScreen" @close-chart="showChart=false"/>
  </div>
</template>

<script>
// @ is an alias to /src
import store from "../store";
import PopMenu from "../components/PopMenu.vue";
import LayerMenu from "../components/LayerMenu.vue";
import basemaps from "@/data/basemaps.json";
import Dashboard from '@/views/Dashboard.vue';
import html from '@/scripts/html-helpers.js';
import watermark from '@/assets/watermark.png';

import {
  LMap,
  LIcon,
  LMarker,
  LControlLayers,
  LPopup,
  LPolyline,
  LPolygon,
  LRectangle,
  LTileLayer,
  LLayerGroup,
  LControl,
  LGeoJson,
} from "vue2-leaflet";
import { latLngBounds, latLng, CRS } from "leaflet";
import consts from '../scripts/consts';

const points = [
  { lat: 10.79, lng: 102.14 },
  { lat: 14.525, lng: 107.0343 },
];
const bounds = latLngBounds(points);
export default {
  name: "Home",
  props:{
    windowHeight: Number,
    windowWidth: Number
  },

  components: {
    LMap,
    LIcon,
    LMarker,
    LControlLayers,
    LPopup,
    LPolyline,
    LPolygon,
    LRectangle,
    LTileLayer,
    LLayerGroup,
    LControl,
    LGeoJson,
    PopMenu,
    LayerMenu,
    Dashboard,
  },

  data() {
    const zoom = 8;
    const center = [11.5, 104.9];
    const geojson = store.getters.geojson;
    const esiaGeoJson = store.getters.esiaGeoJson;
    const iesiaGeoJson = store.getters.iesiaGeoJson;
    const epcGeoJson = store.getters.epcGeoJson;

    return {
      fillColor: "#e4ce7f",
      watermark,
      //marker: latLng(11.5, 104.9),
      zoom,
      center,
      bounds: bounds,
      mapOptions: {
        zoomControl: true,
        attributionControl: true,
        zoomSnap: true,
      },
      tileProvider: basemaps[0],
      expandMenu: false,
      expandLayerMenu: false,
      geojson: geojson,
      esia: esiaGeoJson,
      iesia: iesiaGeoJson,
      epc: epcGeoJson,
      points: points,
      showChart: false,
      showEsiaLayer: true,
      showIesiaLayer: true,
      showEpcLayer: true,
      
    };
  },
  computed: {
    options() {
      const fillColor = this.fillColor;
      return {
        onEachFeature: this.onEachFeatureFunction,
        style: function (feature) {
          if (feature.properties.lic_type === "ESIA") {
            return {
              weight: 2,
              color: consts.colors.esia,
              opacity: 1,
              fillColor: consts.colors.fill,
              fillOpacity: 0.1,
            };
          } else if(feature.properties.lic_type === "IESIA"){
            return {
              weight: 2,
              color: consts.colors.iesia,
              opacity: 1,
              fillColor: consts.colors.fill,
              fillOpacity: 0.1,
            };
          }else if(feature.properties.lic_type === "EPC"){
            return {
              weight: 2,
              color: consts.colors.epc,
              opacity: 1,
              fillColor: consts.colors.fill,
              fillOpacity: 0.2,
            };
          }else {
            return {
              weight: 2,
              color: consts.colors.others,
              opacity: 1,
              fillColor: consts.colors.fill,
              fillOpacity: 0.1,
            };
          }
        },
      };
    },
    onEachFeatureFunction() {
      return (feature, layer) => {
        var table = html.create_table(feature);
        var options = { permanent: false, sticky: true };  
        layer.bindPopup(table, options);
      };
    },

    mapScreen() {
      return this.showChart? this.isLandscape()? "width: 50%; float:right" : "height: 45%; float:top"
              : '';
    },
    
    chartScreen() {
      return this.isLandscape()? "width: 50%; float:left" : "height: 45%; float:bottom";
    }
  },
  methods: {
    smallScreen(){
      return this.windowWidth < 600;
    },  
    isLandscape() {
      return this.windowWidth > this.windowHeight;
    },
    menuSelected(index){
      if(index === 0){
        this.showChart = !this.showChart;
      }
    },
    onBasemapChanged(basemap){
      this.tileProvider = basemap;
    },
    toggleEsiaLayer(value){
      this.showEsiaLayer = value;
    },
    toggleIesiaLayer(value){
      this.showIesiaLayer = value;
    },
    toggleEpcLayer(value){
      this.showEpcLayer = value;
    },
    onMenuChange(value){
      this.expandMenu = value;
    }
  },
  watch: {
    zoom() {
      // console.log(this.windowWidth);
    },
  },
};
</script>


<style scoped>
.home {
  width: 100%;
  height: 100%;
}

.pop-menu {
  background: #fff;
  padding: 0 0.5em;
  border: 1px solid #aaa;
  border-radius: 0.1em;
}


.custom-control-watermark {
  font-size: 150%;
  font-weight: bolder;
  color: #0f0;
  text-shadow: #555;
  opacity: 0.5;
}

.leaflet-container {
  font-family: 'Nokora', sans-serif;
  font-size: 14px ;
} 

</style>
