<template>
    <div class="field__radio" :class="[value === v_value?'field__radio--checked':'']">
        <label :for="id" @click="onClick">
            <input type="radio" :value="label" :name="name" :id="id" /> 
            <img :src="url" width="22"/>
            {{ label }} 
            
                
        </label>
        
    </div>    
</template>


<script>
export default {
    name: 'InputRadio',
    props: {
        value: {},
        v_value: {},
        label: {
            type: String,
            default: ''
        },
        name: {
            type: String,
            default: ''
        },
        id: {
            type: String,
            default: ''
        },
        url: {
            type: String,
            default: ''
        },
    },
    methods:{
        onClick(){
            this.$emit('input', this.v_value);
            console.log(this.v_value);
        }
    }
}
</script>