import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

Vue.config.productionTip = false
import "leaflet/dist/leaflet.css"
import projects from './data/project.json';
store.state.projects = projects;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
